@font-face {
  font-family: Oswald;
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: Oswald;
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: Lato-regular;
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
.content__title {
  margin: 0;
  font-family: Oswald, sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-transform: none;
}
.content__promo-banner {
  position: relative;
  display: grid;
  justify-items: center;
  padding: 27px 20px 0;
  margin-bottom: 20px;
}
.content__promo-banner::before {
  content: "";
  width: 100%;
  height: 302px;
  position: absolute;
  z-index: -5;
  background-image: url("../../img/cat-background-mobile.jpg");
  background-color: #5eaa2f;
  background-position: center;
  background-repeat: no-repeat;
}
.content__advantages {
  margin-bottom: 49px;
}
.content__add-on-products {
  position: relative;
  margin-bottom: 35px;
  padding: 17px 20px 0;
  border-top: 1px solid #ebebeb;
}
.content__add-on-products::before {
  content: "";
  display: block;
  width: 200vw;
  height: 2px;
  position: absolute;
  top: 40px;
  left: -100vw;
  z-index: -5;
  background-color: #68b738;
}
.content__form {
  margin-bottom: 32px;
  font-family: Oswald, sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  border-top: 1px solid #d9d9d9;
}
.content__event-promo {
  border-top: 1px solid #ebebeb;
}
@media (min-width: 768px) {
  .content__title {
    font-size: 60px;
    line-height: 60px;
  }
  .content__add-on-products {
    padding: 0 30px;
    margin-bottom: 75px;
    border-top: none;
  }
  .content__add-on-products::before {
    top: 34px;
  }
  .content__form {
    margin-bottom: 60px;
    font-size: 20px;
    border-top: none;
  }
  .content__promo-banner {
    justify-items: start;
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 0;
  }
  .content__promo-banner::before {
    content: none;
  }
  .content__catalog-choosing {
    margin-bottom: 45px;
  }
  .content__advantages {
    margin-bottom: 103px;
  }
  .content__story {
    border-top: 1px solid #e6e6e6;
  }
  .content__event-promo {
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .content__add-on-products {
    display: grid;
    grid-template-columns: 1fr 245px;
    column-gap: 80px;
  }
  .content__add-on-products::before {
    top: 35px;
  }
  .content__form {
    width: 1440px;
    margin: 0 auto 79px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .content__promo-banner {
    width: 1220px;
    box-sizing: border-box;
    position: static;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 160px 72px 237px;
    justify-items: start;
    align-items: start;
    padding: 36px 0 0 80px;
    margin: 0 auto 83px;
  }
  .content__promo-banner::before {
    content: "";
    width: 50%;
    height: 694px;
    left: 50%;
    top: 0;
    background-image: url("../../img/cat-background-desktop.jpg");
    background-position: left;
    background-size: cover;
  }
  .content__catalog-choosing {
    width: 1220px;
    margin: 0 auto 71px;
  }
  .content__advantages {
    width: 1220px;
    margin: 0 auto 126px;
  }
  .content__story {
    width: 1220px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 433px 1fr;
    grid-template-rows: 206px 165px 227px;
    border-top: none;
  }
  .content__event-promo {
    margin-bottom: 87px;
  }
}
