.site-menu__button {
  width: 24px;
  height: 24px;
  padding: 0;
  position: relative;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.site-menu__list {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}
@media (max-width: 767px) {
  .site-menu--closed .site-menu__list {
    display: none;
  }
  .site-menu--closed .site-menu__button::before {
    content: "";
    width: 24px;
    height: 1.5px;
    position: absolute;
    top: 3px;
    left: 0;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .site-menu--opened .site-menu__list {
    position: absolute;
    top: 65px;
    right: 0;
    left: 0;
    z-index: 10;
    border-top: 1px solid #e6e6e6;
  }
  .site-menu--opened .site-menu__button::before,
  .site-menu--opened .site-menu__button::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 4px;
    width: 25px;
    height: 1.5px;
    background-color: #000000;
  }
  .site-menu--opened .site-menu__button::before {
    transform: rotate(46.64deg);
    box-shadow: none;
  }
  .site-menu--opened .site-menu__button::after {
    transform: rotate(-46.64deg);
  }
}
@media (min-width: 768px) {
  .site-menu__button {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  .site-menu__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 9px;
  }
}
