.catalog-type {
  padding: 0 20px;
}
.catalog-type__link {
  width: fit-content;
  display: flex;
  align-items: center;
  padding: 20px 0 22px;
  font-family: Oswald, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
.catalog-type__link:hover .catalog-type__link-arrow {
  width: 26px;
}
.catalog-type__link:focus-visible {
  outline: none;
}
.catalog-type__link:focus .catalog-type__link-arrow,
.catalog-type__link:focus-visible .catalog-type__link-arrow {
  width: 26px;
}
.catalog-type__link:active {
  opacity: 0.3;
}
.catalog-type__link:active .catalog-type__link-arrow {
  width: 26px;
}
.catalog-type__link-arrow {
  display: block;
  position: relative;
  width: 18px;
  height: 2px;
  margin-left: 16px;
  background-color: #000000;
}
.catalog-type__link-arrow::after {
  content: "";
  position: absolute;
  right: -11px;
  top: -4px;
  border: 5px solid rgba(0, 0, 0, 0);
  border-left: 7px solid #000000;
}
@media (min-width: 768px) {
