  .catalog-type__description {
    display: grid;
    grid-template-columns: 54% 1fr;
  }
  .catalog-type__description::after {
    content: "";
    display: block;
    justify-self: end;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .catalog-type__description--slim-cat::after {
    width: 200px;
    height: 211px;
    margin-top: 4px;
    margin-right: 34px;
    background-image: url("../../img/cat-back--slim.svg");
