.packing {
  display: flex;
  justify-content: space-between;
}

.packing__type {
  font-size: 14px;
  color: @special-dark-grey;
}

.packing__price {
  font-size: 14px;
  color: @special-dark-grey;
}

@media (min-width: @tablet-width) {
  .packing {
    justify-content: end;
  }

  .packing__type {
    font-size: 16px;
    line-height: 20px;
  }

  .packing__price {
    margin: 0;

    flex-basis: 30%;
    text-align: right;

    font-size: 16px;
    line-height: 20px;
  }
}

@media (min-width: @desktop-width) {
  .packing {
    justify-content: initial;
  }

  .packing__type {
    flex-basis: 80%;
  }

  .packing__price {
    flex-basis: initial;
    text-align: left;
  }
}
